<template>
    <div class="tag" :style="{ backgroundColor: getColor(tagValue)}">
        {{tagValue}}
    </div>
</template>

<script>
export default {
    name: "TagComponent",
    props:['tagValue'],
    data() {
        return {
            colorTags: {
                'aws-lambda': '#febd69',
                'aws-api-gateway': '#febd69',
                'aws-cloudfront': '#febd69',
                'aws-s3': '#febd69',
                'aws-ec2': '#febd69',
                'aws-dynamodb': '#febd69',
                'python': '#ffe873',
                'web-development': '#d0d0d0',
                'prompt-engineering': '#d0d0d0',
                'backend-development': '#d0d0d0',
                'aws-engineering': '#d0d0d0',
                'ml-engineering': '#d0d0d0',
            }

        }
    },
    methods: {
        getColor(tag){
            if (tag in this.colorTags){
                return this.colorTags[tag]
            }
            else {
                return '#eee'
            }
        }
    }
}
</script>

<style scoped>
.tag {
    color: black;
    /*font-weight: lighter;*/
}
</style>